* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("https://res.cloudinary.com/dratcv9yz/image/upload/v1727447287/bg_tlgqag.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 50px;
  position: relative;
}

.obj-width {
  max-width: 1200px;
  margin: auto;
}

nav {
  z-index: 1000;
  background-color: #212529cc;
  width: 100%;
  position: fixed;
  top: 0;
  box-shadow: 0 7px 10px #0000004d;
}

.navbar {
  justify-content: space-around;
  align-items: center;
  padding: 1.3rem 0;
  display: flex;
}

.navbar .logo {
  color: #fff;
  letter-spacing: 2px;
  font-size: 2.2rem;
  text-decoration: none;
}

#search-input {
  color: #333;
  background-color: #fffc;
  border: 2px solid #cfcfcf;
  border-radius: 25px;
  outline: none;
  width: 100%;
  max-width: 400px;
  padding: 12px 15px;
  font-size: 16px;
  transition: all .3s;
}

#search-input:focus {
  border-color: #cfcfcf;
  box-shadow: 0 0 8px #fff6;
}

#search-button {
  color: #333;
  cursor: pointer;
  background-color: #ffffffb3;
  border: none;
  border-radius: 25px;
  margin-left: 10px;
  padding: 12px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

#search-button:hover {
  color: #fff;
  background-color: #212529b3;
}

form {
  align-items: center;
  gap: 10px;
  display: flex;
}

#nav-buttons {
  gap: 10px;
  display: flex;
}

.nav-btn {
  color: #333;
  cursor: pointer;
  background-color: #ffffffb3;
  border: none;
  border-radius: 25px;
  padding: 10px 15px;
  font-size: 16px;
  transition: background-color .3s;
}

.nav-btn:hover {
  color: #fff;
  background-color: #212529b3;
}

#blog-container {
  z-index: 10;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 3rem 0;
  display: flex;
}

.blog-card {
  cursor: pointer;
  background-color: #fffc;
  border: 1px solid #21252980;
  border-radius: 10px;
  width: 1500px;
  height: 520px;
  margin: 10px;
  padding: 1rem;
  transition: box-shadow .3s;
}

.blog-card:hover {
  box-shadow: 4px 4px 10px #0003;
}

.blog-card img {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 350px;
  margin-bottom: 1rem;
}

.blog-card h2 {
  color: #333;
  font-size: 1.7rem;
  font-weight: 500;
}

.blog-card p {
  padding-top: .7rem;
  font-size: 1.2rem;
}

footer {
  text-align: center;
  color: #333;
  background-color: #f8f8f8;
  border-top: 1px solid #ccc;
  padding: 20px;
  font-family: Arial, sans-serif;
}

footer a {
  margin-bottom: 10px;
  display: inline-block;
}

footer img {
  max-width: 100%;
  height: auto;
}

@media (width <= 500px) {
  nav {
    padding: 0 20px;
  }

  .navbar .logo {
    font-size: 1.8rem;
  }

  #search-input {
    width: 150px;
    padding: 10px;
  }

  .blog-card {
    width: 100%;
  }
}
